import { createStore } from 'vuex'

export default createStore({
  state: {
    logintoken:null,
    dashboard:false,
    id:0,
    cuenta:"",
    pass:"",
    nombre:"",
    rol_id:0
  },
  mutations: {
    token(state, valor){
      state.logintoken = valor;
      
    },
    dashboard(state, valor){
      state.dashboard= valor
  },
  id(state, valor){
    state.id=valor
  },
  cuenta(state, valor){
    state.cuenta=valor
  },
  pass(state, valor){
    state.pass=valor
  },
  nombre(state, valor){
    state.nombre=valor
  },
  rol_id(state, valor){
    state.rol_id=valor
  },
},
  actions: {
  },
  modules: {
  }
})
