<template>

    <!-- MODAL PARA NUEVO REGISTRO  -->
 <button type="button" id="modalaltaroles" class="btn btn-success" data-toggle="modal" data-target="#nuevorol" data-whatever="@mdo">Nuevo Rol</button>  
<div class="modal fade" id="nuevorol"  data-backdrop="static" data-keyboard="false"  aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Nuevo Rol</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true" >&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <form>
         <div class="form-group">
    <label for="exampleFormControlInput1">Nombre del Rol</label>
    <input  type="email" class="form-control"  placeholder="Nombre del Rol" v-model="nombre">
  </div>
  <div>
      <h3>Politicas:</h3>
     <div  v-for="item in politica" :key="item.id">
  
  <label>
    <div>
    {{item.politicas}}
   </div>
   <div>
  <input type="checkbox" :value="item.id" v-model="politicas" :id="item.id" />
 </div>
</label>
</div>
<p>  {{politicas}}</p>
  </div>
        </form>
      </div>
      <div class="modal-footer">
        <button type="button" id="cerrarmodalrol" class="btn btn-secondary" data-dismiss="modal">Volver</button>
        <button type="button" @click='Guardar()'  class="btn btn-primary">Guardar</button>
      </div>
    </div>
  
<!--fin del MOdal-->
</div>
</div>
 
</template>

<script>
import axios from "axios";
export default {
    data(){
        return{
               
             
               nombre:"",
               politicas:[],
               politica:[]
        }
    },
     mounted(){
        
         this.cargarpoliticas();/// carga los roles en el select
        
     },
   methods:{
        async Guardar(){
      let post = {
                
                
                nombre:this.nombre,
                politicas:this.politicas
  
  }

   console.log(post);
  
     try{ 
      
 const result= await axios.post('http://localhost:3000/roles/', post)
    console.log(result)
     if(result.data.message=='Debe indicar el campo [nombre] con sus politicas en el body'){
                     this.$swal({icon:'error',
                       title: 'Verifique Rol y sus politicas',
                       button:'OK' })
                      return
                }
    if(result.data.message=="Rol existente"){
      this.$swal({icon:'error',
                       title: 'Rol existente',
                       button:'OK' })
    }else{
           if(result.data.message=="Rol Creado"){
             this.$swal.fire({icon:'success',
                       title: 'Datos guarda Correctamente',
                       button:'OK' })
                    this.nombre="",
                    this.politicas=""
                   
        /*   const recargarusuario= document.getElementById("recargausuarios");
             recargarusuario.click()*/
          /// cerrar modal ////
              const cerrarmodal= document.getElementById("cerrarmodalrol");
              cerrarmodal.click()
           } 
             
               
        


    }  
     

                

     }catch(error){
        if(error.request.status==401){
         this.$swal({icon:'error',
                       title: 'Usuario No Autorizado',
                       button:'OK' })
                       return
                       
     }
     }
     
  },
cargarpoliticas:async function(){
         try{
           const politica = await axios.get('http://localhost:3000/politicas')
           if(politica.status == 400){
                    this.$swal({icon:'error',
                               title: 'Error al cargar politicas',
                               button:'OK' })
                               /*this.roles.push('no hay roles en la base')*/
                                return
             }
             this.politica= politica.data
         }catch(error){

             console.log(error)

         }
         
         
         },

        
   
         
}

}
</script>

<style scoped>
#modalusuarioAlta{
  text-align: left;
   margin-bottom: 8px;
  font-size: 10px;
}
</style>