<template>

    <!-- MODAL PARA ACTUALIZAR -->
<!--<button type="button" id="modalusuarioActualizar" class="btn btn-primary" data-toggle="modal" data-target="#actualizar" data-whatever="@mdo">Modificar Usuario</button>  -->

<div  id="actualizar"  data-backdrop="static" data-keyboard="false"  aria-labelledby="ModalLabel" aria-hidden="true">
  <div class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="ModalLabel">Modificación de Usuario</h5>
        <button @click="Volver()" type="button" class="close" aria-label="Close">
          <span aria-hidden="true" >&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <form>
   <div class="form-group">
    <label for="FormControlInput1">cuenta</label>
    <input  type="email" class="form-control" id="FormControlInput1"   placeholder="Correo Electronico" v-model="cuenta">
  </div>
   <div class="form-group">
    <label for="FormControlInput1">Contraseña</label>
    <input type="password" class="form-control" id="FormControlInput1" placeholder="Contraseña" v-model="pass">
  </div>
  <div class="form-group">
    <label for="FormControlInput1">Nombre y Apellido</label>
    <input type="text" class="form-control" id="FormControlInput1" placeholder="Nombre y Apellido"  v-model="nombreApe">
  </div>
 
      <label for="rol">Roles</label>
        <select v-model="rol" class="form-control" id="rol">
            <option v-for = 'rol in roles' :key="rol.id" :value="rol.id">{{rol.nombre}}</option>
        </select>
  
      </form>
      </div>
      <div class="modal-footer">
        <button type="button" @click="Volver()" class="btn btn-secondary" >Volver</button>
        <button type="button" @click='Guardar()'  class="btn btn-primary">Guardar</button>
      </div>
   </div>
  
<!--fin del MOdal-->
</div>
</div>
 
</template>

<script>
import axios from "axios";

export default {
     props:{
        usuario:{
          type: Array,
        },
      },
    
    data(){
     
        return{
               id:0,
               roles: [],
               cuenta: "",
               pass:"",
               nombreApe:"",
               rol:0
                       }
    },
     mounted(){
        
         this.cargarRol();/// carga los roles en el select
               this.id=this.$store.state.id,
               this.cuenta= this.$store.state.cuenta,
               this.pass=this.$store.state.pass,
               this.nombreApe=this.$store.state.nombre,
               this.rol=this.$store.state.rol_id
             
                       
         
        
         
     },
      
     
   methods:{
        Volver(){
            this.$router.push({name:'Usuarios'})
        } , 

        async Guardar(){
      let post = {
               
                cuenta: this.cuenta,
                pass: this.pass,
                nombre:this.nombreApe,
                rol_id:this.rol
  
  }

   console.log(post);

  
     try{ 
    
 const result= await axios.put('http://localhost:3000/usuarios/'+this.id, post, {headers:{
 
 'Authorization': 'Bearer '+this.$store.state.logintoken,
 'poli':'Modificacion Usuarios'
 }
 })
 
    console.log(result)
    if(result.data=="Usuario inexistente"){
      this.$swal({icon:'error',
                       title: 'Usuario inexistente',
                       button:'OK' })
    }else{
           if(result.data=='Usuario Actualizado'){
             this.$swal.fire({icon:'success',
                       title: 'Datos guarda Correctamente',
                       button:'OK' })
                    this.cuenta="",
                    this.pass="",
                    this.nombreApe="",
                    this.rol=0
      
          /// vuelve a usuarios ////
               this.$router.push({name:'Usuarios'})
           } 
              
        


    }  
     

                

     }catch(error){
       
         if(error.request.status==401){
         this.$swal({icon:'error',
                       title: 'Usuario No Autorizado',
                       button:'OK' })
                       return
                       
     }

     }
     
  },
cargarRol:async function(){
         try{
           const roles = await axios.get('http://localhost:3000/roles')
           if(roles.data.message== 'no se encontraron roles'){
                    this.$swal({icon:'error',
                               title: 'No se encontraron roles',
                               button:'OK' })
                               /*this.roles.push('no hay roles en la base')*/
                                return
             }
             this.roles= roles.data
         }catch(error){

             console.log(error)

         }
         
         
         },

         

         
 
         
}


}
</script>

<style scoped>

</style>