<template>
<div >
  <!--<div v-show="modal" class="modal.open" >-->
<!-- Button trigger modal -->
<div v-if="this.$store.state.logintoken">
<button @click="cerrarsesion" type="button" id="cerrarsesion" class="btn btn-danger">Cerrar sesion</button>
</div>
<div v-if="!this.$store.state.logintoken">
<div >
<button id="modal" type="button" class="btn btn-primary" data-toggle="modal" data-target="#exampleModal" >
  Ingresar
</button>
</div>

 <AltasUsuarios/>
 
 
</div>

<div class="modal fade"  id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div  class="modal-dialog">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Login</h5>
     <button  type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <form v-on:submit.prevent>
          <div class="form-group">
            <label for="recipient-name" class="col-form-label" >Cuenta:</label>
            <input type="text" class="form-control" v-model="usuario">
          </div>
          <div class="form-group">
            <label for="message-text" class="col-form-label">Contraseña:</label>
            <input type="password" class="form-control" v-model="password" >
          </div>
        </form>
      </div>
      <div class="modal-footer">
        <button type="button" id="closemodal" class="btn btn-secondary" data-dismiss="modal">Cerrar</button>
        <button @click='login' type="button" class="btn btn-primary">Ingresar</button>
     </div>
    </div>
   
  </div>
</div>



</div>
</template>
<script>

import axios from "axios";
import AltasUsuarios from "./AltasUsuarios.vue";
export default ({
 name: 'Login',
  components:{
    
      AltasUsuarios,
      
},

 
  props:{
      modal: {
       type:Boolean,
       default:false
      } ,
  },
  data(){
    
    return{
      usuario:null,
      password:null,
      //result:null,
      info:null,
      
    }
  },

 
  
  methods:{  
  
  
  cerrarsesion(){
this.$store.commit('dashboard', false), 
this.$store.commit('token', null)
this.$router.push({name:'Home'})
  },
  
  async login(){
      let post = {
                cuenta: this.usuario,
                pass: this.password,
  
  }

   console.log(post);
  
     try{ 
 const result= await axios.post('http://localhost:3000/auth/login', post)
    
    if(result.data=="Verifique datos"){
      this.$swal({icon:'error',
                       title: 'Login Incorrecto',
                       button:'OK' })
    }else{
      this.$swal.fire({icon:'success',
                       title: 'Login Correcto',
                       button:'OK' })
      //alert('Bienvenido '+ post.cuenta)
       ////varible de store guardar token /////
      console.log(result.data.token)
                this.$store.commit('token', result.data.token); 
                this.$store.commit('dashboard', result.data.message);
          /// cerrar modal ////
              const cerrarmodal= document.getElementById("closemodal");
              cerrarmodal.click()
    } 
             

     }catch(error){
         console.log(error)
     }
  
  }
  },
  
    
 
  
  })
   

</script>

