import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue';
import Login from '../components/Login.vue';
import Encabezado from '../components/Encabezado.vue';
import Piepagina from '../components/Piepagina.vue';
import Usuarios from '../views/Usuarios.vue';
import Roles from '../views/Roles.vue';
import Dashboard from '../components/Dashboard.vue';
import AltasUsuarios from '../components/AltasUsuarios.vue';
import ModificarUsuario from '../components/ModificarUsuario.vue';
import AltasRoles from '../components/AltasRoles.vue';

const routes = [
  {
    path: '/Home',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: Login 
  },
  {
    path: '/encabezado',
    name: 'Encabezado',
    component: Encabezado 
  },
  {
    path: '/Piepagina',
    name: 'Piepagina',
    component: Piepagina
  },
  {
    path: '/Usuarios',
    name: 'Usuarios',
    component: Usuarios
  },
  {
    path: '/Dashboard',
    name: 'Dashboard',
    component: Dashboard
  },
  {
    path: '/AltasUsuarios',
    name: 'AltasUsuarios',
    component: AltasUsuarios
  },
  {
    path: '/ModificarUsuario',
    name: 'ModificarUsuario',
    component: ModificarUsuario
  },
  {
    path: '/',
    name: 'Roles',
    component: Roles
  },
  {
    path: '/AltasRoles',
    name: 'AltasRoles',
    component: AltasRoles
  },
 
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
