<template>
<div>
   
    <h2> Listado de Roles</h2>
<AltasRoles/>
 
<dx-data-grid
 id="iddatagrid"
:data-source="arraydedatos"
:columns="columns"

:showRowLines="true"
:allow-column-reordering="true"
:allow-column-resizing="true"
:showColumnLines="true"
:show-borders="true"
:rowAlternationEnabled="true"
:loadPanel="loadPanel"
:filterRow="filterRow"
:searchPanel="searchPanel"
:noDataText="noDataText"
ref="grillaPaginacionNormal"
:stateStoring="stateStoring"
:paging="paging"
:pager="pager"
>

</dx-data-grid >
   <div class="row">
       <div class="col"> 
<button class="btn btn-danger" @click="limpiarFiltro()"><i class="fas fa-broom"></i> Limpiar</button>
<button class="btn btn-primary" @click="reload()" id="recargausuarios"><i class="fas fa-sync-alt"></i> Refresh</button>
</div>
</div>
</div>
</template>
<script>
// Importamos JQuery
//import $ from 'jquery'
import{ DxDataGrid} from 'devextreme-vue/data-grid';
import AltasRoles from '../components/AltasRoles.vue';
/*import ModificarUsuario from '../components/ModificarUsuario.vue';*/
import axios from "axios";

export default ({

     components:{
      DxDataGrid,
      AltasRoles,
      /*ModificarUsuario*/
},

     data(){
         return{
                usuario:[],
                data:[],
                id:0,          
                roles: [], // <-- La lista de roles
                cuenta: "",
                pass: "",
                nombre:"",
                rol_id:0,
             

            
              stateStoring:{
              enabled:true,
              type:"localStorage",
             storageKey:"grillaPaginacionNormal"
            },
            paging:{
                visible:true,
                pageSize:5
            },
            pager:{
                visible:true,
                showPageSizeSelector:true,
                showNavegationButtons:true,
                showInfo:true,
                infoText:'Pagina {0} de {1} ({2} items totales)',
                allowedPagesSizes:[5,20,40]
            },
            arraydedatos:[
                /*{id:1, nombre:"Juan", formasDeManejarse:"auto"},
                 {id:2, nombre:"Nico", formasDeManejarse:"bus"},
                 {id:3, nombre:"Ruben", formasDeManejarse:"moto"},*/

             ],
         
             columns:[
                 {
                 Caption:"Id",
                 dataField:"id",
                 alignment:"center"
                 },
                 {
                 Caption:"Nombre",
                 dataField:"nombre",
                 alignment:"center"
                 },
               
                 {
                     caption:"Opciones",
                     type:"buttons",
                     
                     buttons:[
                        {
                             nane:"editar",
                             hint:"Editar Persona",
                             Text:"Editar",
                             icon:"edit",
                             visible:true,
                             onClick:async(e)=>{
                               
                                    
                                    const data = await e.row.data
                                    console.log(data.id)
                                                        
                                  
                                  /*const modal= document.getElementById('modalusuarioActualizar');
                                  modal.click();*/
                               this.$store.commit('id', data.id) 
                               this.$store.commit('cuenta', data.cuenta)      
                               this.$store.commit('pass', data.pass)       
                               this.$store.commit('nombre', data.nombre)      
                               this.$store.commit('rol_id', data.rol_id)   
               
                                 
                                   this.$router.push({name:'ModificarUsuario'})              
                                
                             }

                        
                      },
                         {
                             nane:"borrar",
                             hint:"Borrar Persona",
                             Text:"Borrar",
                             icon:"trash",
                             visible:true,  
                             onClick:this.EliminarRol
                         }
                     ]
                 }

             ],
             headerFilter:{
             visible:"True" 
             },
             noDataText:"No se encontraron Resultado",
             filterRow:{
                 visible:"True",
                 applyFilter:"auto",
                 resetOperationText:"reset"
             },
             loadPanel:{
                 visible:"True",
                 Text:"Cargando.."
             },
             searchPanel:{
                 visible:"True",
                 width:240,
                 placebolder:"Buscar...",
             }
                     
              
         }
         
     },
     
     mounted(){
         this.cargarDatos();/// llamaremos al metodo que carga nuestros datos
         
     },
     methods:{
      


cargarDatos:async function(){
     try{
          //// hacemos referencia a la grilla para poder ejecutar el loadPanel para que loading se visible////
             this.$refs["grillaPaginacionNormal"].instance.beginCustomLoading("Cargando..");


  
      
 const arraydedatos = await axios.get('http://localhost:3000/roles',{headers:{
 
 'Authorization': 'Bearer '+this.$store.state.logintoken,
 'poli':'Lista de Roles'
 }
 })
    
    if(arraydedatos.data.message=="no se encontraron roles" ){
      this.$swal({icon:'error',
                       title: 'No se encontraron roles',
                       button:'OK' })
                       return
    }

     
  this.arraydedatos= arraydedatos.data
  
  
         
         

              


    this.$refs["grillaPaginacionNormal"].instance.endCustomLoading();
}
catch(error){
         console.log(error)
         
                       
     
}
             },
           
        limpiarFiltro(){
              this.$refs["grillaPaginacionNormal"].instance.clearFilter();
        },   
        reload:async function(){
               //////ponemos bandera TRUE y llamamos al metodo que volvera a hace la consulta de la carga de datos//
              /* this.banderaAgregarDatos=true;*/
               this.cargarDatos();
        },

        async EliminarRol(e){
             
             const id = e.row.data.id
             console.log(id)
             /////////////////////
 

  const resultado = await this.$swal({
  title: 'Estas Seguro?',
  text: "No podras revertir esto!",
  icon: 'warning',
  showCancelButton: true,
  confirmButtonColor: '#3085d6',
  cancelButtonColor: '#d33',
  confirmButtonText: 'Si, Eliminar!'
})
try{ 
  if (resultado.isConfirmed) {
    const result= await axios.delete('http://localhost:3000/roles/'+id ,{headers:{
 
 'Authorization': 'Bearer '+this.$store.state.logintoken,
 'poli':'Bajas Usuarios'
 }
 })
    console.log(result)
    if(result.data=="Rol inexistente"){
      this.$swal({icon:'error',
                       title: 'Rol inexistente',
                       button:'OK' })
    }else{
           if(result.data=='Rol eliminado'){
             this.$swal({icon:'success',
                       title: 'Rol Eliminado',
                       button:'OK' })
                       this.cargarDatos();
               }       
         }  
        
  }
 
 
  }catch(error){
        if(error.request.status==401){
         this.$swal({icon:'error',
                       title: 'Usuario No Autorizado',
                       button:'OK' })
                       return
                       
     }
     
  }
 

}  
}
        
})       
 
    
             //////////////////


         
         
     
    









</script>
<style lang="scss">

</style>



