<template>
 
<div>
   
    <h2> Listado de Usuarios</h2>
  <AltasUsuarios/>
 
<dx-data-grid
 id="iddatagrid"
:data-source="arraydedatos"
:columns="columns"

:showRowLines="true"
:allow-column-reordering="true"
:allow-column-resizing="true"
:showColumnLines="true"
:show-borders="true"
:rowAlternationEnabled="true"
:loadPanel="loadPanel"
:filterRow="filterRow"
:searchPanel="searchPanel"
:noDataText="noDataText"
ref="grillaPaginacionNormal"
:stateStoring="stateStoring"
:paging="paging"
:pager="pager"
>

</dx-data-grid >
   <div class="row">
       <div class="col"> 
<button class="btn btn-danger" @click="limpiarFiltro()"><i class="fas fa-broom"></i> Limpiar</button>
<button class="btn btn-primary" @click="reload()" id="recargausuarios"><i class="fas fa-sync-alt"></i> Refresh</button>
</div>
</div>
</div>
</template>
<script>

import{ DxDataGrid} from 'devextreme-vue/data-grid';
import AltasUsuarios from '../components/AltasUsuarios.vue'
/*import ModificarUsuario from '../components/ModificarUsuario.vue';*/
import axios from "axios";
export default ({

     components:{
      DxDataGrid,
      AltasUsuarios,
      /*ModificarUsuario*/
},

     data(){
         return{
                usuario:[],
                data:[],
                id:0,          
                roles: [], // <-- La lista de roles
                cuenta: "",
                pass: "",
                nombre:"",
                rol_id:0,
             

            
              stateStoring:{
              enabled:true,
              type:"localStorage",
             storageKey:"grillaPaginacionNormal"
            },
            paging:{
                visible:true,
                pageSize:5
            },
            pager:{
                visible:true,
                showPageSizeSelector:true,
                showNavegationButtons:true,
                showInfo:true,
                infoText:'Pagina {0} de {1} ({2} items totales)',
                allowedPagesSizes:[5,20,40]
            },
            arraydedatos:[
                /*{id:1, nombre:"Juan", formasDeManejarse:"auto"},
                 {id:2, nombre:"Nico", formasDeManejarse:"bus"},
                 {id:3, nombre:"Ruben", formasDeManejarse:"moto"},*/

             ],
         
             columns:[
                 {
                 Caption:"Id",
                 dataField:"id",
                 alignment:"center"
                 },
                 {
                 Caption:"Cuenta",
                 dataField:"cuenta",
                 alignment:"center"
                 },
                {
                 Caption:"Nombre",
                 dataField:"nombre",
                 alignment:"center"
                 },
                  {
                 Caption:"Rol",
                 dataField:"roles.nombre",
                 alignment:"center"
                 },
                 {
                  caption:"Icono",
                  dataField:"formasDeManejarse",
                  width:100,
                  allowFiltering: false,
                  allowSorting:false,
                  alignment:"center",
                  cellTemplate:function(container,options){

                      if(options.data.formasDeManejarse == "auto"){
                          $('<div class="text-center">')
                          .css({"font-size":"20px","color":"#236B8E"})
                          .append($('<span title="Auto"><i class="fas fa-car"></i></span>'))
                          .appendTo(container);
                      }else{
                          if(options.data.formasDeManejarse == "moto"){
                          $('<div class="text-center">')
                          .css({"font-size":"23px","color":"#FF0000"})
                          .append($('<span title="Moto"><i class="fas fa-motorcycle"></i></span>'))
                          .appendTo(container);
                      }else{
                           $('<div class="text-center">')
                          .css({"font-size":"23px","color":"#008f39"})
                          .append($('<span title="Autobus"><i class="fas fa-bus"></i></span>'))
                          .appendTo(container);
                      }
                    }
                  }
                 },
                 {
                     caption:"Opciones",
                     type:"buttons",
                     
                     buttons:[
                        {
                             nane:"editar",
                             hint:"Editar Persona",
                             Text:"Editar",
                             icon:"edit",
                             visible:true,
                             onClick:async(e)=>{
                               
                                    
                                    const data = await e.row.data
                                    console.log(data.id)
                                                        
                                  
                                  /*const modal= document.getElementById('modalusuarioActualizar');
                                  modal.click();*/
                               this.$store.commit('id', data.id) 
                               this.$store.commit('cuenta', data.cuenta)      
                               this.$store.commit('pass', data.pass)       
                               this.$store.commit('nombre', data.nombre)      
                               this.$store.commit('rol_id', data.rol_id)   
               
                                 
                                   this.$router.push({name:'ModificarUsuario'})              
                                
                             }

                        
                      },
                         {
                             nane:"borrar",
                             hint:"Borrar Persona",
                             Text:"Borrar",
                             icon:"trash",
                             visible:true,  
                             onClick:this.EliminarUsuario
                         }
                     ]
                 }

             ],
             headerFilter:{
             visible:"True" 
             },
             noDataText:"No se encontraron Resultado",
             filterRow:{
                 visible:"True",
                 applyFilter:"auto",
                 resetOperationText:"reset"
             },
             loadPanel:{
                 visible:"True",
                 Text:"Cargando.."
             },
             searchPanel:{
                 visible:"True",
                 width:240,
                 placebolder:"Buscar...",
             }
                     
              
         }
         
     },
     
     mounted(){
         this.cargarDatos();/// llamaremos al metodo que carga nuestros datos
         
     },
     methods:{
      


cargarDatos:async function(){
     try{
          //// hacemos referencia a la grilla para poder ejecutar el loadPanel para que loading se visible////
             this.$refs["grillaPaginacionNormal"].instance.beginCustomLoading("Cargando..");

////////////////////////////
         /*async login(){
      let post = {
                cuenta: this.usuario,
                pass: this.password,
  
  }

   console.log(post);*/
  
      
 const arraydedatos = await axios.get('http://localhost:3000/usuarios',{headers:{
 
 'Authorization': 'Bearer '+this.$store.state.logintoken,
 'poli':'Lista de Usuarios'
 }
 })
    
    if(arraydedatos.status==400){
      this.$swal({icon:'error',
                       title: 'No se encontraron usuarios',
                       button:'OK' })
                       return
    }

     
  this.arraydedatos= arraydedatos.data
  
  
         
         
///////////////////////////         
         /////simulacion de demora BD///
             /*await new Promise(resolve=>{
                 setTimeout(()=>{
                     resolve(10);
                 }, 6000);

             });*/
             ///// luego de realizar demora , cargararemos los datos normalmente////
              /*this.arraydedatos=[
                 {id:1, nombre:"Juan", formasDeManejarse:"auto"},{id:2, nombre:"Nicolas", formasDeManejarse:"moto"},
                 {id:3, nombre:"Gonzalez", formasDeManejarse:"bus"},{id:4, nombre:"Pedro", formasDeManejarse:"bus"},
                 {id:5, nombre:"Alejenadro", formasDeManejarse:"bus"},{id:6, nombre:"Ramiro", formasDeManejarse:"moto"},
                 {id:7, nombre:"Hernan", formasDeManejarse:"auto"},{id:8, nombre:"Agustin", formasDeManejarse:"auto"},
                 {id:9, nombre:"Alejandro", formasDeManejarse:"bus"},{id:10, nombre:"Ismael", formasDeManejarse:"moto"},
                 {id:11, nombre:"Romina", formasDeManejarse:"moto"},{id:12, nombre:"Rodolfo", formasDeManejarse:"moto"},
                 {id:13, nombre:"Alex", formasDeManejarse:"bus"},{id:14, nombre:"Marta", formasDeManejarse:"auto"},
                 {id:15, nombre:"Mateo", formasDeManejarse:"bus"},{id:16, nombre:"walter", formasDeManejarse:"bus"},
                 {id:17, nombre:"Marcos", formasDeManejarse:"auto"},{id:18, nombre:"Daniel", formasDeManejarse:"moto"},
                 {id:19, nombre:"Santiago", formasDeManejarse:"auto"},{id:20, nombre:"gabriel", formasDeManejarse:"moto"},

              ];*/
              //////cuando realicemos un refresh entran datos /// 
            /*  if (this.banderaAgregarDatos){
                  this.arraydedatos.push(
                 {id:21, nombre:"Samuel", formasDeManejarse:"bus"},{id:22, nombre:"David", formasDeManejarse:"bus"},
                 {id:23, nombre:"German", formasDeManejarse:"auto"},{id:24, nombre:"Mauro", formasDeManejarse:"moto"},
                 {id:25, nombre:"Leonardo", formasDeManejarse:"auto"},
                  );
              }*/
              ///// finaliza el Loading /////
    this.$refs["grillaPaginacionNormal"].instance.endCustomLoading();
}
catch(error){
         console.log(error)
         
                       
     
}
             },
           
        limpiarFiltro(){
              this.$refs["grillaPaginacionNormal"].instance.clearFilter();
        },   
        reload:async function(){
               //////ponemos bandera TRUE y llamamos al metodo que volvera a hace la consulta de la carga de datos//
              /* this.banderaAgregarDatos=true;*/
               this.cargarDatos();
        },

        async EliminarUsuario(e){
             
             const id = e.row.data.id
             console.log(id)
             /////////////////////
 

  const resultado = await this.$swal({
  title: 'Estas Seguro?',
  text: "No podras revertir esto!",
  icon: 'warning',
  showCancelButton: true,
  confirmButtonColor: '#3085d6',
  cancelButtonColor: '#d33',
  confirmButtonText: 'Si, Eliminar!'
})
try{ 
  if (resultado.isConfirmed) {
    const result= await axios.delete('http://localhost:3000/usuarios/'+id ,{headers:{
 
 'Authorization': 'Bearer '+this.$store.state.logintoken,
 'poli':'Bajas Usuarios'
 }
 })
    console.log(result)
    if(result.data=="Usuario inexistente"){
      this.$swal({icon:'error',
                       title: 'Usuario inexistente',
                       button:'OK' })
    }else{
           if(result.data=='Usuario Eliminado'){
             this.$swal({icon:'success',
                       title: 'Usuario Eliminado',
                       button:'OK' })
                       this.cargarDatos();
               }       
         }  
        
  }
 
 
  }catch(error){
        if(error.request.status==401){
         this.$swal({icon:'error',
                       title: 'Usuario No Autorizado',
                       button:'OK' })
                       return
                       
     }
     
  }
 

}  
}
        
})       
 
    
             //////////////////


         
         
     
    


// Importamos JQuery
import $ from 'jquery'






</script>
